.Subexpr {
}

.Subexpr button {
  height: 10vh;
  width: 10vh;
  background-size: contain;
  background-position: center;
  display: inline-block;
  background-repeat: no-repeat;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Subexpr button:disabled {
  opacity: 0.2;
}

.Subexpr .Done {display: block;}

.ButtonWrap {
  display: inline-block;
}

.ButtonWrap:first-of-type {
  display: block;
}

.Subexpr .up {
  background-image: url('./img/cursor-up.svg');
}

.down {
  background-image: url('./img/cursor-down.svg');
}

.left {
  background-image: url('./img/cursor-left.svg');
}

.right {
  background-image: url('./img/cursor-right.svg');
}

.Preview {
  height: 10ex;
}

.Done button {
  min-width: 6em;
}

.buttonup{
  display: block;
}



