.Step {
  border: 1px solid black;
  -webkit-box-shadow: 5px 5px 4px black;
          box-shadow: 5px 5px 4px black;
  width: 90%;
  margin: 10px auto;
}

.Step button {
  padding: 5px 10px;
  margin-bottom: 20px;
  font-size: large;
  background-color: white;
  border: 2px solid #EDD;
  cursor: pointer;
}

.Done {
  background-image: url('./icons8-ok.svg');
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  background-size: contain;
  display: inline-block;
}



